.TicketContainer {
  height: 100%;
  width: 100%;
  display: grid;
  align-items: center;
  justify-items: center;
}

.fuentePrin.swap {
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
}

.loaderTicketCont {
  align-self: center;
  transform: translateY(-20vh);
}

.smartContainer {
  width: 100%;
  max-height: calc(100vh - 80px);
  height: calc(100vh - 80px);
  max-width: 650px;
  border-radius: 6px;
  overflow: hidden;
  border: 1px solid transparent;
  -webkit-transition: 0.25s;
  transition: 0.25s;
  align-self: baseline;
}

.TicketAnim {
  width: 100%;
  height: 150px;
}

.TicketConfirm {
  align-self: flex-start;
}

.ticketStep2Border {
  border: 1px solid white;
}

.desaparecer {
  transition: 0.2s;
  opacity: 0;
}

.aparecer {
  transition: 0.2s;
  opacity: 1;
}

.TicketPaymentProof {
  opacity: 0;
  color: white !important;
  display: grid;
  width: 100%;
  grid-template-rows: repeat(2, 1fr);
  height: 200%;
  transition: 0.3s;
  transform: translateY(0%);
  /*transform: translateY(-50%);*/
}

#TFileUpload {
  width: 100%;
  height: 100%;
  opacity: 0;
  position: absolute;
  z-index: 2;
  cursor: pointer;
}

.ctaPaymentCont {
  max-height: calc(100vh - 80px);
}

.ctaPaymentProof {
  position: relative;
  display: grid;
  align-items: center;
  justify-items: center;
  /* grid-template-rows: 20vh 16vh 1fr; */
  max-height: calc(100vh - 80px);
  grid-template-rows: 30vh 8vh 1fr;
}

.titlePayment {
  align-self: center;
  max-width: 550px;
  text-align: center;
  font-weight: bold;
  margin: 0;
  font-size: 26px;
  align-items: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  justify-content: space-around;
}
.contenidoTicket {
  display: grid;
  /*align-items: center;*/
  justify-items: center;
  padding-top: 20px;
  align-items: center;
  padding-bottom: 30px;
}

.TicketmsgPayment {
  margin-top: 20px;
  text-align: center;
  color: white;
  font-size: 16px;
}

.msgPayment {
  color: white;
  text-align: center;
  max-width: 550px;
}

.droppedImgPaymentProof {
  background: black;
  position: relative;
  max-height: calc(100vh - 0px);
}

.ticketSlide {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: 200%;
  max-height: calc(100vh - 80px);
  height: calc(100vh - 80px);
  transition: 0.3s;
}

#rejectedOne {
  animation-name: atenualoPerro;
  animation-duration: 1s;
  animation-iteration-count: infinite;
  z-index: 1;
  animation-timing-function: cubic-bezier(1, 1, 1, 1);
}

@keyframes atenualoPerro {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}

.rejectedCont {
  position: relative;
  width: 73px;
  height: 73px;
}

.rejectedCont img {
  position: absolute;
}

.ticketHeader {
  display: grid;
  grid-template-columns: 1fr;
  border-bottom: 2px solid white;
  grid-template-rows: repeat(2, 1fr);
  justify-items: center;
}

.TicketTitle {
  color: white;
  margin: 0 !important;
  font-size: 22px;
  display: flex;
  align-items: center;
}

.TicketDetail {
  /*background: black;*/
  display: grid;
  grid-template-rows: 20vh 1fr 70px;
  width: 100%;
  height: calc(100vh - 80px);
}

.TicketButton {
  /*background: black;*/
}

.TloaderSec {
  width: 100%;
  height: 100%;
  background: white;
  position: absolute;
  opacity: 0.85;
  z-index: 2;
  display: grid;
  align-items: center;
  justify-items: center;
}

.TicketDetailItem {
  width: calc(100% - 60px);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 30px;
  height: 50px;
}

.TicketItemTitle {
  font-weight: bold;
}

.fuenteTicket .iconSty {
  margin-left: 10px;
}

.fuenteTicket {
  color: white;
  display: flex;
}
.TicketDetailItem p {
  color: white;
  margin: 0;
  font-size: 17px;
}

.confirm.TicketTitle {
  margin: 0 0 15px 0 !important;
}

.fuenteTicket.value p {
  display: block;
  margin: 0 0 0 2px;
  max-width: 250px;
  overflow: hidden;
  padding-left: 0;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.explorerBlock {
  display: flex;
  text-decoration: none;
  color: white;
  align-items: center;
  justify-content: center;
  margin-left: 5px;
  font-size: 14px;
  transform: scale(1);
  transition: 0.2s;
}

.explorerBlock:hover {
  transform: scale(1.3);
}

.p.tooltiptext {
  width: auto;
  padding: 3px 5px;
  font-size: 9px !important;
}

/* - - - - - CONFIRMATIONCOUNTER- - - - - - - - - - - - - - - - - - - - - - - - - - - - */

.contCanvasConfirmation {
  width: auto;
  height: auto;
  position: relative;
  display: grid;
}

.confirmationCounterText {
  width: 80px;
  height: 50%;
  position: absolute;
  align-self: center;
  justify-self: center;
  display: grid;
  align-items: center;
  justify-items: center;
}

.confirmationCounterText p {
  font-size: 30px;
  color: white;
  margin: 0;
}

.confirmationCounterText span {
  color: #ffffff91;
  margin-left: 5px;
  font-size: 18px;
}

