.swing-in-bottom-bck {
  -webkit-animation: swing-in-bottom-bck 1s
    cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
  animation: swing-in-bottom-bck 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) both;
}

@-webkit-keyframes swing-in-bottom-bck {
  0% {
    -webkit-transform: rotateX(-70deg);
    transform: rotateX(-70deg);
    -webkit-transform-origin: bottom;
    transform-origin: bottom;
    opacity: 0;
  }

  100% {
    -webkit-transform: rotateX(0);
    transform: rotateX(0);
    -webkit-transform-origin: bottom;
    transform-origin: bottom;
    opacity: 1;
  }
}

@keyframes swing-in-bottom-bck {
  0% {
    -webkit-transform: rotateX(-70deg);
    transform: rotateX(-70deg);
    -webkit-transform-origin: bottom;
    transform-origin: bottom;
    opacity: 0;
  }

  100% {
    -webkit-transform: rotateX(0);
    transform: rotateX(0);
    -webkit-transform-origin: bottom;
    transform-origin: bottom;
    opacity: 1;
  }
}

@-webkit-keyframes flip-2-hor-bottom-fwd {
  0% {
  }

  40% {
    -webkit-transform: translateY(0) translateZ(0) rotateX(0) scale(1);
    transform: translateY(0) translateZ(0) rotateX(0) scale(1);
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
  }

  100% {
    -webkit-transform: translateY(100%) translateZ(160px) rotateX(180deg)
      scale(1.3);
    transform: translateY(100%) translateZ(160px) rotateX(180deg) scale(1.3);
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
  }
}

@keyframes flip-2-hor-bottom-fwd {
  0% {
  }

  40% {
    -webkit-transform: translateY(0) translateZ(0) rotateX(0) scale(1);
    transform: translateY(0) translateZ(0) rotateX(0) scale(1);
    -webkit-transform-origin: 50% 100%;
    transform-origin: 50% 100%;
  }

  100% {
    -webkit-transform: translateY(100%) translateZ(160px) rotateX(180deg)
      scale(1.3);
    transform: translateY(100%) translateZ(160px) rotateX(180deg) scale(1.3);
    -webkit-transform-origin: 50% 0%;
    transform-origin: 50% 0%;
  }
}

